.row__poster {
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    transition: transform 450ms;
    margin-right: 10px;
  }
  
  .row__posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
  }
  
  .row__posters::-webkit-scrollbar {
    display: none;
  }
  
  .row__poster:hover {
    transform: scale(1.08);
  }
  
  .row__posterLarge {
    max-height: 250px;
  }
  
  .row__posterLarge:hover {
    transform: scale(1.09);
  }
  
  .row {
    /* margin-top: 50px; */
    margin-left: 20px;
    color: #fff;
  }
  
  .row h3 {
    font-weight: 400;
    margin-left: 20px;
  }
  
  /* Responsive Styles */
  
  /* For tablets and smaller screens */
  @media (max-width: 768px) {
    .row__poster {
      max-height: 80px;
      margin-right: 8px;
    }
  
    .row__posterLarge {
      max-height: 180px;
    }
  
    .row__posters {
      padding: 15px;
    }
  
    .row {
      margin-left: 10px;
    }
  
    .row h3 {
      font-size: 1.2rem;
      margin-left: 15px;
    }
  }
  
  /* For mobile screens */
  @media (max-width: 480px) {
    .row__poster {
      max-height: 60px;
      margin-right: 5px;
    }
  
    .row__posterLarge {
      max-height: 150px;
    }
  
    .row__posters {
      padding: 10px;
    }
  
    .row {
      margin-left: 5px;
    }
  
    .row h3 {
      font-size: 1rem;
      margin-left: 10px;
    }
  }